<template>
    <div class="ActivityDetails">
        <Carousel :type="type" :num="num[id]"></Carousel>
        <ActivityA v-if="id==0"></ActivityA>
  </div>
</template>
<script>
import Carousel from '@/components/Carousel'
import ActivityA from '@/activity/ActivityA'
export default {
  name: 'ActivityDetails',
  components: {
    Carousel,ActivityA
  },
  data () {
    return {
        type:'activitya',
        num:[4],
		id: this.$route.query.id,
    }
  },
  methods:{
    isType(){
      if(this.id == 0){
        this.type = "activitya"
      }
    }
  },
  mounted(){
   
  }
}
</script>
<style lang="less" scoped>

</style>


