<template>
    <div class="ActivityA">
        <div class="border_box">许麟庐艺术馆在烟台市博物馆开馆——市民可免费参观</div>
        <div class="activitydetails_text">
            2015年10月30日讯，许麟庐艺术馆开馆仪式于10月29日上午在市博物馆一
			楼中央大厅举行,许麟庐师生友情展也在许麟庐艺术馆同步展出。今后,烟台
			市民可免费在许麟庐艺术馆欣赏到大师的珍贵作品。下午,烟台市博物馆还
			举办了“写意人生——许麟庐书画艺术”学术研讨会,就许麟庐先生书画艺术研
			究成果进行了深入探讨和交流。<br/><br/>
            许麟庐是我国著名国画家、书法家、书画鉴赏家,烟台蓬莱人,他早年曾师
			从著名画师溥心畲先生,也是齐白石老人的嫡传弟子。按照许麟庐的遗愿
			,许麟庐之子许化迟于2012年和2015年前后3次向烟台市博物馆无偿捐赠
			了许麟庐的书画作品、文房用具、铜像以及图书影音资料等206件/套,
			用于筹建许麟庐艺术馆。<br/><br/>
            其中包括许麟庐的书画作品101幅,生前使用过的文房用具和日常用品
			等42件/套,图书影像资料10件/套,铜像1尊,红木长方桌1张,温家
			宝总理题字(复制品)1幅,老照片50张。在即将开馆之际,许麟庐的
			弟子王少石先生向烟台市博物馆无偿捐赠了许麟庐与王少石往来书
			信10封。这是自1958年建馆以来,烟台市博物馆接收当代书画艺术
			大师个人作品最为全面系统的捐赠。<br/><br/>
            据烟台市博物馆工作人员介绍,为使许麟庐先生的艺术珍品得到有
			效保护,使其艺术得以传承和弘扬,早在2010年11月,许麟庐先生之
			子许化迟先生就与烟台市博物馆签订了一份协议书,决定将许老先生
			的部分艺术作品、文房用具、文献资料、收藏文物等无偿捐赠给烟台
			市博物馆;烟台市博物馆在新馆内专门设立“许麟庐艺术馆”,长期展示
			捐赠的作品、物品。<br/><br/>
            许化迟先生曾于2012年和2015年前后三次向烟台市博物馆无偿捐赠
			了许麟庐先生的书画作品、文房用具、铜像以及图书影音资料等206
			件/套,用于筹建许麟庐艺术馆,其中包括许麟庐先生的书画作品101
			幅,生前使用过的文房用具和日常用品等42件/套,图书影像资料10件/套,
			铜像1尊,红木长方桌1张,温家宝总理题字(复制品)1幅,老照片50张。
            这三次捐赠是自1958年市博物馆建馆以来当代书画大师个人作品方面最为全面系统的捐赠。
			据了解,接受的捐赠(许麟庐先生遗物),数量最多且艺术价值最高的当属许麟庐先生的101幅
			书画作品,其中既有难得一见的许麟庐的书法作品,又有他生平擅长绘画的花果鸟兽的花鸟
			题材作品,还有部分山水和民间玩具类的题材作品。其作品的创作年代大致从20世纪50年
			代延续至2011年,较完整地反映了许麟庐先生几十年艺术生涯的发展轨迹和累累硕果,彰
			显了他在艺术道路上孜孜不倦的探索精神。<br/><br/>
        </div>
  </div>
</template>
